@use 'base/variables' as v;
@use 'base/mixins' as m;

.olvide {
    margin-top: 5rem;

    .axity {
        @include m.textoDegradado(v.$turquesa);
    }

    input[type="submit"] {
        @include m.boton(v.$turquesa, v.$blanco);
    }

    .acciones {
        a {
            color: v.$turquesa;
        }
    }
}